body,
input,
textarea,
select,
button {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
}
body {
  background-color: #322e2d;
  color: white;
  background-image: url('assets/bg_antracit.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}
.background_img {
  background-image: url('assets/bg_login.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}
@media only screen and (-webkit-min-device-pixel-ratio: 1.3),
  only screen and (min--moz-device-pixel-ratio: 1.3),
  only screen and (-o-min-device-pixel-ratio: 2.6/2),
  only screen and (min-device-pixel-ratio: 1.3),
  only screen and (min-width: 1024px) {
  .background_img {
    background-image: url('assets/bg_login@2x.jpg');
  }
}

input {
  padding: 0.5rem;
  border: 1px solid #95989a;
  border-radius: 0.25rem;
}
@import url('../node_modules/material-design-icons/iconfont/material-icons.css');
@import url('../node_modules/react-grid-layout/css/styles.css');
@import url('assets/flaticon.css');

/* react-grid disabling animation */
.react-grid-item {
  -webkit-transition: all 0s;
  -o-transition: all 0s;
  transition: all 0s;
}



